import React, { Component } from "react";
import './maintainance.scss';
import { withRouter } from "react-router-dom";


export class Maintainance extends Component {
    constructor(props) {
        super(props);

        this.state = {


        };
    }


    render() {
        let message='';
         message=this.props && this.props.location && this.props.location.state && this.props.location.state.errorMessage;
        return (
            <div className="maintainance-main">
         <div className="main-heading-maintainance">We'll be back soon!</div>
         <div className="subheading-maintainance">{message}</div>

            </div>
        );
    }
}


export default withRouter(Maintainance);
